<template>
    <v-container class="pa-0 ma-0" fluid>
        <v-app-bar
                app
                color="#FFFFFF"
                elevation="3"
                class="app-bar-gradient-back"
                dark
        >
            <v-row>
                <v-col class="text-left pa-0">
                    <v-btn text icon dark
                           tile
                           height="100%"
                           @click="$router.push({name: 'settings'})">

                        <v-icon>mdi-cog-outline</v-icon>
                    </v-btn>
                </v-col>
                <v-col class="text-center">
                    <v-toolbar-title>{{yorProfileText}}</v-toolbar-title>
                </v-col>
                <v-col class="text-right" style="padding-top: 0; padding-bottom: 0">
                    <v-btn class="app-bar-button" small tile style="display: inline;" text dark
                           @click="openProfileEdit">
                        {{editText}}
                    </v-btn>
                </v-col>
            </v-row>
        </v-app-bar>


        <v-row class="text-center justify-center pa-0 ma-0" style="margin-top: 56px!important; margin-bottom: 30px; width: 100%">
            <PublicProfile
                    :profile-image="profileImage"
                    :header-image="headerImage"
                    :username="username"
                    :known-languages="knownLanguages"
                    :languages-to-learn="languagesToLearn"
                    :description="description"
                    :birth-date="birthDate"
                    :avg-rating="avgRating"
                    :num-ratings="numRatings"
                    :my-rating-is-visible="false"
            >
            </PublicProfile>
        </v-row>
        <div style="margin-bottom: 70px"></div>

        <v-snackbar
                v-model="snackbar"
                :timeout="4000"
                style="bottom: 76px"
        >
            {{ snackbarText }}
            <v-btn
                    color="primary"
                    text
                    @click="snackbar = false"
            >
                {{closeText}}
            </v-btn>
        </v-snackbar>
    </v-container>

</template>

<script>
    import PublicProfile from "./PublicProfile";
    import {mapActions, mapState} from 'vuex';
    import Constants from "../../store/Constants";

    export default {
        name: "ProfileHome",
        data: () => ({
            dialog: false,
            snackbar: false,
            snackbarText: null,
            headerImage: null,
            yorProfileText: null,
            editText: null,
            closeText: null,
            confirmText: null,
            askingForLogOutConfirmationText: null
}),
        computed: {
            ...mapState('user', ['username', 'description',
                'knownLanguages', 'languagesToLearn', 'avgRating', 'numRatings']),
            birthDate: {
                get() {
                    let birth = this.$store.getters['user/getBirthDate'];
                    if (birth != null && birth != undefined) {
                        return this.$i18n.d(new Date(birth), 'short');
                    } else {
                        return undefined;
                    }
                }
            },
            profileImage: {
                get() {
                    let profileImg = this.$store.getters['user/getProfileImg'];
                    if (profileImg != null && profileImg != undefined) {
                        return profileImg;
                    } else {
                        return this.$store.getters['user/getGenderDefaultProfileImgByUserGender'];
                    }
                }
            }
},

        methods: {
            ...mapActions('user', ['logout']),

            openProfileEdit() {
                this.$router.push({name: 'profileEdit', params: {id: this.$store.getters['user/getID']}})
            }
},
        created() {
            this.yorProfileText = this.$i18n.t('messages.TEXT_YOUR_PROFILE');
            this.editText = this.$i18n.t('messages.TEXT_EDIT');
            this.closeText = this.$i18n.t('messages.TEXT_CLOSE');
            this.confirmText = this.$i18n.t('messages.TEXT_CONFIRM');
            this.askingForLogOutConfirmationText = this.$i18n.t('messages.TEXT_ARE_YOU_SURE_YOU_WANT_TO_LOG_OUT');
        },
        mounted() {
            this.headerImage = Constants.IMG_USER_PROFILE_HEADER;
        },
        components: {
            PublicProfile
        }
    }
</script>

<style scoped>
    .bottom-centered-buttons {
        width: 92.5%;
        left: 50%;
        transform: translate(-50%, 0);
        position: absolute;
    }

    .app-bar-button {
        height: 100% !important;
        min-width: unset !important;
    }
</style>
